import { HttpClient, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ICommand, IHttpGetParams, IHttpOptions, RX_BUNDLE, RxCommandFactoryService } from '@helix/platform/shared/api';
import { Observable, of } from 'rxjs';
import { IDocumentDefinition } from './document-definition.interface';

@Injectable({
  providedIn: 'root'
})
export class RxDocumentDefinitionService {
  constructor(private httpClient: HttpClient, private rxCommandFactoryService: RxCommandFactoryService) {}

  get(documentDefinitionName: string, options?: IHttpGetParams): Observable<IDocumentDefinition> {
    return this.httpClient.get<IDocumentDefinition>(this.getUrl(documentDefinitionName), options);
  }

  getNew(): Observable<IDocumentDefinition> {
    return of({
      allowOverlay: false,
      documentSchema: null,
      name: null,
      scope: RX_BUNDLE.definitionScopeTypes.bundle
    });
  }

  create(documentDefinition: IDocumentDefinition): Observable<HttpResponse<any>> {
    return this.httpClient.post(this.getUrl(), documentDefinition, { observe: 'response' });
  }

  update(documentDefinition: IDocumentDefinition, options?: IHttpOptions): Observable<any> {
    return this.httpClient.put(this.getUrl(documentDefinition.name), documentDefinition, options);
  }

  delete(documentDefinitionName: string): Observable<any> {
    return this.httpClient.delete(this.getUrl(documentDefinitionName));
  }

  revertCustomization(documentDefinitionName: string): Observable<any> {
    const revertCustomizationCommand: ICommand = this.rxCommandFactoryService.forResourceType(
      'com.bmc.arsys.rx.application.document.command.RevertDocumentDefinitionCommand'
    );

    return revertCustomizationCommand.execute({ documentDefinitionName });
  }

  rename(oldDocumentDefinitionName: string, newDocumentDefinitionName: string): Observable<any> {
    const renameCommand: ICommand = this.rxCommandFactoryService.forResourceType(
      'com.bmc.arsys.rx.application.document.command.RenameDocumentDefinitionCommand'
    );

    return renameCommand.execute({
      name: oldDocumentDefinitionName,
      newName: newDocumentDefinitionName
    });
  }

  private getUrl(documentDefinitionName?: string): string {
    return documentDefinitionName
      ? `/api/rx/application/document/documentdefinition/${encodeURIComponent(documentDefinitionName)}`
      : '/api/rx/application/document/documentdefinition';
  }
}
